body {
    background: #FAFAFA;
}

.app {
    margin: 16px;
}

.app-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 32px;
}

.app-header > h2 {
    color: #346cb0;
    padding: 0 16px;
    display: block;
    margin: 0;
}

.app-header > span {
    padding: 4px 8px;
    font-size: 15px;
    font-weight: bold;
    border-radius: 4px;
    text-transform: capitalize;
}

h4, h2 {
    margin: 4px 0;
}

.container {
    margin: 0px 16px;
}

.group {
    margin: 16px 0;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    background: #FFFFFF;
}

.group-graphs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 8px;
}

.group-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 16px;
}

.group-title {
    display: block;
    margin: 0;
}

.graph {
    width: calc(33% - 32px);
    height: auto;
    margin: 16px;
    margin-bottom: 24px;
}

.graph-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.group-header:hover {
    background-color: #EEEEEE;
}

.graph-title {
    margin: 0;
    display: block;
}

.clickable {
    cursor: pointer;
}

.chart {
    width: 100%;
    height: auto;
    max-height: 220px;
}

.select-group {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 16px;
    min-width: 220px;
}

select.select-box {
    height: 36px;
    width: auto;
    border: 1px solid rgba(0, 0, 0, 0.2);
    margin: 8px;
    font-size: 14px;
}

.collapse {
    visibility: hidden;
    height: 0;
}

.collapse-button {
    border: none;
    font-size: 14px;
    color: #346cb0;
    outline: none;
    padding: 4px 8px;
}

.collapse-button:hover {
    background-color: #EEEEEE;
    border-radius: 3px;
}

.time-range-options-list {
    clear: both;
    list-style: none;
    display: flex;
    flex-direction: row;
    float: right;
    margin: 0;
}

.time-range-option > button {
    padding: 4px;
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 12px;
}

.time-range-option > button:hover {
    background-color: #EEEEEE;
    border-radius: 3px;
}

.time-range-option > button.active {
    font-weight: bold;
}

.legend {
    clear: both;
    list-style: none;
    margin: 0;
    padding: 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.legend-item {
    font-size: 12px;
    position: relative;
    padding: 4px 4px 4px 16px;
    margin-right: 4px;
    cursor: pointer;
}

.legend-item:hover {
    background-color: #EEEEEE;
}

.legend-item > span {
    color: #000000;
    margin: 0;

}

.legend-item.inactive {
    opacity: 0.3;
}

.legend-item::before {
    content: '\2022';
    font-size: 24px;
    position: absolute;
    left: 4px;
    top: -4px;
}

.text-center {
    width: 100%;
    text-align: center;
}

@media (max-width: 900px) {
    .graph {
        width: calc(50% - 32px);
    }
}

@media (max-width: 400px) {
    .graph {
        width: calc(100% - 32px);
    }
}

@media (min-width: 1900px) {
    .graph {
        width: calc(25% - 32px);
    }
}